import React from 'react';
import './App.css'
import '../node_modules/bootstrap/dist/css/bootstrap.min.css'
import Main from './website/mainpage/main/Main.js'
import { BrowserRouter as Router, Route, Routes ,Switch} from 'react-router-dom'
import Navbar from './website/mainpage/navbar/Navbar.js';
import Register from './website/authentication/Register.js';
import Notfound from './website/notfound/Notfound.js';
import Login from './website/authentication/Login.js';
import ForgotPassword from './website/authentication/forgotPassword';
import ResetPassword from './website/authentication/resetPassword';
import UpdatePassword from './website/authentication/updatePassword';
function App() {
  const [signedIn, setsignedIn] = React.useState(false);
  const [user, setuser] = React.useState({});
  return (
    <>
    <Navbar
        signedIn={signedIn}
        setsignedIn={setsignedIn}
        user={user}
        setuser={setuser}
      />
    
      <Switch>
        <Route
          exact
          path="/"
          component={() => (
            <Main
              signedIn={signedIn}
              user={user}
            />
          )}
        />
        <Route
          exact
          path="/register"
          component={() => (
            <Register
              setsignedIn={setsignedIn}
              setuser={setuser}
            />
          )}
        />
        <Route
          exact
          path="/login"
          component={() => (
            <Login
              setsignedIn={setsignedIn}
              setuser={setuser}
            />
          )}
        />
        <Route
          exact
          path="/forgotPassword"
          component={() => <ForgotPassword />}
        />
        <Route
          exact
          path="/resetPassword/:token"
          component={() => (
            <ResetPassword
              setsignedIn={setsignedIn}
              setuser={setuser}
            />
          )}
        />
        <Route
          exact
          path="/updatePassword"
          component={() => <UpdatePassword />}
        />

       <Route path="*">
          <Notfound />
        </Route>
      </Switch>
      
    </>
    
  );
}

export default App;
