import React from "react";
import Aboutme from "./Aboutme";
import Contact from "./Contact";

const Footer=(props)=> {
  return (
    <div className="footer" id="footer" >
      <Aboutme />
      <Contact 
      user={props.user}  
      signedIn={props.signedIn}/>
      <p 
      className="last"
        style={{
          textAlign: "center",
          gridColumnStart: 1,
          gridColumnEnd: 3,
          fontSize: "small",
        }}
      >
         2024 WebMaker. All rights reserved.
      </p>
    </div>
  );
}
export default Footer;