import React from 'react'
import '../../../../node_modules/bootstrap/dist/css/bootstrap.min.css'
import pricing from'./../pricing/Pricing'
import Myprofile from './Myprofile'
import {Link} from 'react-router-dom'
import axios from 'axios'
const Navbar = (props) => {
  const signout = () => {
    axios.get("http://127.0.0.1:3000/api/v1/users/logout/");
    props.setsignedIn(false);
    return;
  };

  return (
  
        <div>
      <nav class="bg-dark  navbar navbar-expand-lg  navbar-dark  ">
  <div class="container">
    <a class="navbar-brand h1 text-white" href="#">WebMaker</a>
    <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span class="navbar-toggler-icon "></span>
    </button>
    <div class="collapse navbar-collapse" id="navbarSupportedContent">
      <ul class="navbar-nav me-auto mb-2 mb-lg-0">
        <li class="nav-item">
        <Link
     to="/"
     style={{ color: "whitesmoke", textDecoration: "none" }}
   >
          <a class="nav-link active text-white-50" aria-current="page" href="#">Home</a></Link>
        </li>
        <li class="nav-item">
          <a class="nav-link text-white-50" href="#Pricing">Service</a>
        </li>
        <li class="nav-item">
          <a class="nav-link text-white-50" href="#footer">Contact</a>
        </li>
        <li class="nav-item">
          <a class="nav-link text-white-50"  href="#footer">About</a>
        </li>
      </ul>
      <form class="d-flex align-items-center" role="search">
      {!props.signedIn ?
     ( <div  className="d-flex align-items-center">
      <Link
     to="/login"
     style={{ color: "whitesmoke", textDecoration: "none" }}
   > 
     <a class="nav-link align-items-center mx-4 text-white-50" href="#">login</a>
     </Link>
     <Link
     to="/register"
     style={{ color: "whitesmoke", textDecoration: "none" }}
   > <a><button type="button" class="btn btn-primary">signup</button></a></Link>
   </div>
        ):(<div className="d-flex align-items-center">
          <Myprofile
          user={props.user}  
         setuser={props.setuser}
         setsignedIn={props.setsignedIn}/>
         <a><button type="button" class="btn btn-primary" onClick={()=>signout()}>logout</button></a>
         </div>
        )
      }
      </form>
    </div>
  </div>
</nav>

    </div>
  )
}

export default Navbar;
