import React from 'react'

const Signup = (props) => {
  if(props.signedIn)return(<></> );
  return (
    <div class="container my-5">
         <aside class="bg-primary bg-gradient rounded-3 p-4 p-sm-5 mt-5">
                        <div class="d-flex align-items-center justify-content-between flex-column flex-xl-row text-center ">
                            <div class="mb-4 mb-xl-0 text-xl-start">
                                <div class="fs-3 fw-bold text-white">Sign up now for more details</div>
                                <div class="text-white-50">Sign up for our newsletter for the latest updates.</div>
                            </div>
                            <div class="mt-3 mt-xl-0 ms-xl-4 text-center text-xl-end"> 
                <div class="input-group mb-2">
                {/* <!-- <input class="form-control" type="text" placeholder="Email address..." aria-label="Email address..." aria-describedby="button-newsletter" /> --> */}
                    <button class="btn btn-outline-light btn-lg" id="button-newsletter" type="button">Sign up</button> 
                </div>
                                <div class="small text-white-50">We care about privacy, and will never share your data.</div>
                            </div>
                        </div>
                    </aside>
    </div>
  )
}

export default Signup
